import React from "react";
import { motion, useAnimation } from "framer-motion";
import moment from "moment";

interface GalleryData {
  thumbnailImage: string;
  title: string;
  subtitle: string;
  duration: { from: string; to?: string };
  tags?: string[];
  onClick?: () => void;
}

/** 날짜를 입력하면 YYYY-MM-DD 형식으로 변환 */
function getDate(date: string | undefined): string {
  if (date === undefined) return "ing";
  return moment(date).format("YYYY-MM-DD");
}

const Tag = (props: { tag: string }) => {
  return (
    <div className="w-fit whitespace-nowrap rounded-2xl p-1 px-2 text-xs bg-slate-400 text-slate-100">
      # {props.tag}
    </div>
  );
};

function GalleryItem(props: GalleryData) {
  const templateAnimation = useAnimation();

  return (
    <motion.div
      className="flex flex-col gap-2 w-72 border border-slate-300 bg-slate-100 p-3 rounded-2xl cursor-pointer select-none"
      animate={templateAnimation}
      initial={{
        boxShadow: "0 0px 10px 0px rgba(0, 0, 0, 0.2)",
      }}
      onTap={props.onClick}
      whileHover={{
        boxShadow: "0 0px 15px 0px rgba(0, 0, 0, 0.23)",
        scale: 1.02,
        transition: {
          duration: 0.2,
          ease: "circOut",
        },
      }}
    >
      <div className="flex items-center h-36 bg-slate-50 rounded-3xl overflow-hidden">
        <img className="object-cover" src={props.thumbnailImage} />
      </div>
      <div className="flex flex-col gap-1 p-2">
        <div className="text-slate-500 text-2xl font-bold whitespace-nowrap overflow-ellipsis overflow-hidden">
          {props.title}
        </div>
        <div className="flex flex-col text-slate-400 text-xs">
          <div className="flex">{props.subtitle}</div>
          <div className="flex">
            <div>{getDate(props.duration.from)}</div>
            <div>~</div>
            <div>{getDate(props.duration.to)}</div>
          </div>
        </div>
        <div className="flex gap-1 overflow-x-auto">
          {props.tags?.map((tag) => {
            return (
              <Tag
                key={`gallery/GalleryItem/${props.title},${tag}`}
                tag={tag}
              />
            );
          })}
        </div>
      </div>
    </motion.div>
  );
}

export default GalleryItem;
