import create from "zustand";

interface ModalState {
  isOpen: boolean;

  ModalClose: () => void;
  ModalOpen: () => void;
}
export const useModalStore = create<ModalState>((set) => ({
  isOpen: false,
  selectedKey: "",
  ModalClose: () => set((state) => ({ isOpen: false })),
  ModalOpen: () => set((state) => ({ isOpen: true })),
}));
